*
{
    margin:0;
    padding:0;
}
.home
{
    
    max-width:1000px;
    margin:0 auto;
    margin-top:0px;
    height:100%;
    background-color: rgb(227, 237, 234);
    background-image: url("https://enrollacademy.com/wp-content/uploads/2021/05/ssnhe2-scaled.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    margin-left: 10px;
}


.home__container
{
    height:100vh;
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
}
.home__formContainer {
    box-shadow:1px 4px 12px -2px rgba(0,0,0,4.3);
    margin-top: 0px;
    background:#0575e6;
    padding:20px;
    border-radius:10px;
    width:300px;
    position: absolute;
    margin-left: 1100px;
    height: 90vh;
}
.home__formContainer input{
    border:1px solid #e5e5e5;
    outline:none;
    padding:10px 10px;
    width:80%;
    border-radius:8px;
    margin:10px 0;
    background:transparent;
    color:white;
    font-size:14px;
}
.home__formContainer input::placeholder
{
    color:white;
}
.home__formContainer button{
    background: transparent;
border:1px solid white;
outline:none;
font-size:18px;
color:white;
padding:10px;
width:100px;
margin-top:30px;
border-radius:12px;
margin-left: 90px;
cursor:pointer;
}