*
{
    margin:0;
    padding:0;
}
nav{
    display:flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgb(69, 99, 51);
    padding:10px 0;
}
nav  h1
{
    font-size:30px;
    color:white;
    font-weight:500;
}
nav ul{
    display:flex;
    justify-content: space-around;
    align-items: center;
    width:30%;
   
}
nav ul a{
    font-size:20px;
    font-weight:300;
    color:white;
    text-decoration:none;
}